html{-webkit-text-size-adjust:none}body,p,h1,h2,h3,h4,h5,h6,ul,ol,li,dl,dt,dd,table,th,td,form,fieldset,legend,input,textarea,button,select{margin:0;padding:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body,input,textarea,select,button,table{color:#666;font-size:1.2em;font-family:"Noto Sans KR","Malgun Gohic","맑은 고딕",sans-serif;line-height:1em}body{min-width:320px}img,fieldset{border:0;vertical-align:top}ul,ol{list-style:none}em,address{font-style:normal}a{font-family:"Noto Sans KR",sans-serif;color:#666;text-decoration:none}a:hover,a:active,a:focus{color:#333;text-decoration:none}hr{display:none !important}table{border-collapse:collapse}legend{display:none}input,textarea{-webkit-appearance:none;-webkit-border-radius:0}*,*:after,*:before{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}.clearfix:after{display:block;clear:both;content:""}html,body,#root,.anbang{width:100%;height:100%}#Content{padding:15px;background-color:#d0caff;min-height:100%;padding-top:63px}